



:root {
  --main-color: #B4BD00;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

 body{
  line-height: 16;
 }

html {
  -webkit-text-size-adjust: 100%;
}


::-webkit-scrollbar {
  background:transparent;
  width: 0;
}


@font-face {
